import { useState, useCallback, useEffect, useDeferredValue } from "react"
import { IFilters, IAuditTrail } from "./AuditTrail.types"
import { Box } from "@mui/system"
import FilterInput, {
	IFilterInputProps
} from "../../components/FilterInput/FilterInput"
import FilterSelect, {
	SelectProps
} from "../../components/FilterInput/FilterSelect"
import { Button, Stack } from "@mui/material"
import DateRange from "../../components/FilterInput/DateRange"
import { RangePickerProps } from "antd/lib/date-picker"
import { Table } from "../../components/Table"
import { ColumnType, ITable } from "../../components/Table/Table.types"
import { FilterType } from "react-table"
import moment from "moment"
import { Search } from "@mui/icons-material"
import {
	DirectionEnum,
	PageInfo,
	RoleEnum,
	useAuditTrailsQuery,
	UserAction
} from "../../graphql/generated"
import { DataLoadError } from "../../components/ErrorScreens"
import { Typography } from "antd"

export default function AuditTrail(_props: IAuditTrail) {
	const [filters, setFilters] = useState<IFilters>({
		direction: DirectionEnum["Forward"],
		cursor: null
	})

	const [variables, setVariables] = useState<IFilters>(filters)

	const deferredFilters = useDeferredValue(filters)

	const { data, isLoading, isFetching, isError, refetch } = useAuditTrailsQuery(
		variables,
		{
			keepPreviousData: true,
			refetchOnWindowFocus: false,
			select: useCallback(
				(queryData: any): { actions: UserAction[]; pageInfo: PageInfo } => {
					const filtered =
						queryData?.data?.userActions?.edges?.map(
							(edge: any) => edge.node
						) || []
					return {
						actions: filtered,
						pageInfo: queryData?.data?.userActions?.pageInfo
					}
				},
				[]
			)
		}
	)

	const { actions, pageInfo } = data || {}

	useEffect(() => {
		refetch()
	}, [deferredFilters.direction, deferredFilters.cursor])

	const refetchListingQuery = () => {
		setVariables({
			...filters,
			cursor: null,
			direction: DirectionEnum["Forward"]
		})
	}

	const rangeFilterFunction: FilterType<UserAction> = (
		rows,
		ids,
		filterValue
	) => {
		if (!filterValue || (!filterValue[0] && !filterValue[1])) {
			return rows
		}

		return rows.filter(row => {
			return ids.some(id => {
				const rowValue = row.values[id]
				return moment(rowValue).isBetween(filterValue[0], filterValue[1])
			})
		})
	}

	let columns: ColumnType<UserAction>[] = [
		{ Header: "User", accessor: "idpUserName" },
		{
			Header: "Role",
			accessor: "roleName",
			Cell: ({ value }): any => {
				if (value) return String(value)
				return "External user"
			}
		},
		{ Header: "Action", accessor: "actionName" },
		{ Header: "Type", accessor: "actionLevel" },
		{ Header: "Status", accessor: "status" },
		{
			Header: "Time",
			accessor: "actionDate",
			filter: rangeFilterFunction,
			Cell: ({ row }: any) => {
				const date = moment(row.values.actionDate).format("DD/MM/YY")
				return <Typography>{date}</Typography>
			}
		}
	]

	const onRoleChange: SelectProps["onChange"] = value => {
		setFilters({
			...filters,
			roleName: (value as RoleEnum) || null
		})
	}

	const onNameChange: IFilterInputProps<UserAction>["onChange"] = e => {
		setFilters({
			...filters,
			search: e.target.value
		})
	}

	const onDateRangeChange: RangePickerProps["onChange"] = (
		values,
		_formatString
	) => {
		if (values && values[0] && values[1]) {
			setFilters({
				...filters,
				startDateTime: values[0].format("YYYY-MM-DD"),
				endDateTime: values[1].format("YYYY-MM-DD")
			})
		} else {
			setFilters({
				...filters,
				startDateTime: null,
				endDateTime: null
			})
		}
	}

	const setPage: ITable<{}, "cursor">["setPage"] = (_pageNo, direction) => {
		if (direction) {
			setVariables({
				...variables,
				direction: direction as DirectionEnum,
				cursor:
					direction == DirectionEnum.Backward
						? pageInfo?.startCursor
						: pageInfo?.endCursor
			})
		}
	}

	if (isError) {
		return <DataLoadError />
	}

	if (isLoading) {
		;<Box>Loading...</Box>
	}

	return (
		<Box>
			<Box
				mb={2}
				display="flex"
				justifyContent="space-between"
				alignItems="flex-end"
			>
				<Stack direction="row" alignItems="center" spacing={3.5}>
					<FilterInput
						label="Search User"
						value={filters.search}
						onChange={onNameChange}
						endAdornment={<Search fontSize="large" color="action" />}
					/>
					<FilterSelect
						label="Select Role"
						value={filters.roleName}
						onChange={onRoleChange}
					>
						<option label="Super Admin" value={RoleEnum["SuperAdmin"]}>
							Super Admin
						</option>
						<option label="Sub Admin" value={RoleEnum["SubAdmin"]}>
							Sub Admin
						</option>
						<option label="Practice Admin" value={RoleEnum["PracticeAdmin"]}>
							Practice Admin
						</option>
					</FilterSelect>
					<DateRange label="Select Date" onChange={onDateRangeChange} />
				</Stack>
				<Box pl={2} justifySelf="flex-end">
					<Button variant="contained" onClick={refetchListingQuery}>
						Search
					</Button>
				</Box>
			</Box>
			<Table
				columns={columns}
				data={actions || []}
				paginationType="cursor"
				rowsPerPage={pageInfo?.perPage || 0}
				setPage={setPage}
				metaData={{
					hasNextPage: pageInfo?.hasNextPage,
					hasPreviousPage: pageInfo?.hasPreviousPage,
					total_records: pageInfo?.totalRecords
				}}
				disabled={isLoading || isFetching}
				dataLoading={isLoading || isFetching}
			/>
		</Box>
	)
}
